import React, { useState } from 'react';
import Tex2SVG from "react-hook-mathjax";
import MCOptionPricer from '../ProjectCode/MCOptionPricer';
import SyntaxHighlighter from 'react-syntax-highlighter';
import { docco, tomorrowNight } from 'react-syntax-highlighter/dist/esm/styles/hljs';

const MCOptionPage = ({ }) => {

    return <div className="page-info-container">
            <MCOptionPricer />
            <h2>OPTIONS</h2>
            <div> <p className="page-info-block"> Options are a contract that give the owner the right to buy or sell a given asset at a specific price by a specific time. Options are sold at the cost of a premium paid upfront - this allows the seller to profit if the option expires and recover from potential losses otherwise. Due to their nature, vanilla options carry substantial risk for the seller and limit the risk for the buyer. The premium that is charged relies heavily on the conditions of the option contract and the model use to predict future stock prices.</p> </div>
            <div> 
                <p className="page-info-block">There are three main styles of vanilla options:
                <li><a className="custom-console-blue">American</a>: Option is based on current asset price can be used any time before it expires. </li>
                <li><a className="custom-console-blue">European</a>: Option is based on current asset price can only be used at expiration. </li>
                <li><a className="custom-console-blue">Asian</a>:    Option is based on average asset price and can be used any time before it expires.</li>
                </p>
            </div>
            <div> 
                <p className="page-info-block">There are two main types of option:
                <li><a className="custom-console-blue">Call</a>: You buy the right to buy the asset- betting the stock will increase even more than the premium. </li>
                <li><a className="custom-console-blue">Put</a>: You buy the right to sell the asset- betting the stock will decrease even more than the premium.</li>
                Naturally, when selling an option you need to invert the strategy. For example, selling a call option means you are betting the stock will not increase more than the premium. 
                </p>
            </div>
            <div> 
                <p className="page-info-block">
                The specific price set in the option contract is called the <a className="custom-console-blue">strike price</a> and the specific time the option must be used by is called the <a className="custom-console-blue">expiration</a> or time to expiry.
                </p>
            </div>
            <h2>MODEL</h2>
            <div> <p className="page-info-block"> If we consider the puzzle of how to price a stock option, what we must first do is create a model to predict the motion of the stock price. From this model, we can hedge our premium against the risk associated with the option. 
            The model requires the following parameters:
            <li><Tex2SVG display="inline" latex="K"/> - Strike Price</li>
            <li><Tex2SVG display="inline" latex="\mu"/> - Stock Drift</li>
            <li><Tex2SVG display="inline" latex="\sigma"/> - Volatility</li>
            <li><Tex2SVG display="inline" latex="S_0"/> - Initial Stock Price</li>
            <li><Tex2SVG display="inline" latex="S(t)"/> - Stock Price at time t</li>
            <li><Tex2SVG display="inline" latex="T"/> - Time to expiry</li>
            <li><Tex2SVG display="inline" latex="\Delta t"/> - Time step</li>
            <li><Tex2SVG display="inline" latex="r_{free}"/> - Risk-free rate of return</li>
            </p>
             </div>
             <div> <p className="page-info-block"> The standard way to model the fluctuations of the stock price is through  <a className="custom-console-yellow">Stochastic Calculus</a>. Using the framework of stochastic calculus and our basic assumptions, we can build our model. 
             <li><a className="custom-console-yellow">Assumption #1</a>: Volatility is constant. </li>
             <li><a className="custom-console-yellow">Assumption #2</a>: Changes in stock price are randomly distributed. </li>
             <li><a className="custom-console-yellow">Assumption #3</a>: The market is arbitrage-free, allowing <Tex2SVG display="inline" latex="\mu = r_{free}"/>. </li>
             </p>
             </div>
             <div> <p className="page-info-block"> From these assumptions we can write the expression for the change in stock price,</p>
             <p className="page-eq-center-block"><Tex2SVG display="block" latex="dS(t) = \mu S(t) dt + \sigma S(t) dW(t)"/>. </p>
             <p className="page-info-block"> Where <Tex2SVG display="inline" latex="dW(t)"/> is the Werner process and returns a random value normally distributed about zero with a standard deviation of <Tex2SVG display="inline" latex="dt"/>.</p>
             </div>
             <div> <p className="page-info-block"> Using this equation, we could jump right into programming the solution using a numerical method to solve the stochastic differential equation. However, this differential equation has an exact solution, famously known as <a className="custom-console-yellow">Geometric Brownian Motion</a>. </p>

             To solve the differential equation exactly, we must use Ito's lemma,
             <p className="page-eq-center-block"><Tex2SVG display="block" latex="d(\ln(F)) = \frac{dF}{F} - \frac{1}{2} \frac{dF dF}{F^2}"/>. </p>
             Where <Tex2SVG display="inline" latex="dS"/> is known from our equation above, so, <Tex2SVG display="inline" latex="dS dS"/> to first order of <Tex2SVG display="inline" latex="dt"/> becomes,
             <p className="page-eq-center-block"><Tex2SVG display="block" latex="dS dS = \sigma^2 S^2 dW^2 = \sigma^2 S^2 dt"/>.</p>
             Plugging this into Ito's lemma and substituting in the expression for <Tex2SVG display="inline" latex="dS/S"/>, we get,
             <p className="page-eq-center-block"><Tex2SVG display="block" latex="d(\ln(S)) = \mu - \frac{1}{2} \sigma^2 + \sigma dW"/>.</p>
             The final step is to integrate both sides and exponentiate the equation,
             <p className="page-eq-center-block"><Tex2SVG display="block" latex="S(t) = S(0) e^{(\mu - \frac{\sigma^2}{2}) t + \sigma W(t)}"/>.</p>
             </div>
             <h2>MC PATHS</h2>
             <div> <p className="page-info-block"> Programming the model we derived above is actually quite a simple task. The Python3 function below takes in the parameters of our model and returns <a className="custom-color-red">num_paths</a> number of paths that are randomly generated.</p></div>

             <SyntaxHighlighter className="code-block-highlighter" language="python" style={tomorrowNight} showLineNumbers={true}>
            {'import numpy as np\ndef get_paths(S0, mu, sigma, times, num_paths):\n    """\n        : input float S0 : Inital Stock Price\n        : input float mu : Average drift\n        : input float sigma : Volatility\n        : input float[] times : Array of time steps in time order\n        : input int num_paths : Number of paths to generate\n\n        : return 2d numpy.array : 2D numpy array of shape(num_paths, len(times))\n    """\n    init = np.tile([S0],(num_paths,1))\n    stand_term = np.tile(np.diff(times) * (mu - 1/2 * sigma**2), (num_paths,1))\n    stoch_term = sigma * np.random.normal(loc=0,\n                                         scale=np.diff(times),\n                                         size=(num_paths,len(times)-1))\n    mc_step = stand_term + stoch_term\n    return np.append(init, S0 * np.exp(np.cumsum(mc_step, axis=1)), axis=1)'}
            </SyntaxHighlighter>
                <div> <p className="page-info-block"> If we generate a large number of paths and plot them with some opacity for some artificially input parameters, the structure of the geometric brownian motion reveals itself.</p>
                <div  className="page-img-block"><img className="page-img" width="100%" src={require("../MC_paths.png")}></img></div>

                </div>
                <h2>PRICING</h2>
                <div> <p className="page-info-block"> Options should be priced based on their expected payout from our MC paths and based on the risk-free rate of return. The expected payout will differ between American, European, and Asian options but the methodology remains the same.</p>
                <p className="page-info-block"> If we choose to calculate for an Asian call option, the expected risk-adjusted payout is calculated as,
                </p>
                <p className="page-eq-center-block"><Tex2SVG display="block" latex="P = avg(max(0,\bar{S}-K)) e^{-rT}"/>. </p>
                Using this expected risk-adjusted payout as the price of the Asian call option, we reduce the risk while maintaining a competative option price.
                 </div>
                 <SyntaxHighlighter className="code-block-highlighter" language="python" style={tomorrowNight} showLineNumbers={true}>
            {'def calc_price_asian_call_option(paths, K, r, T):\n    avg_price_per_path = np.mean(paths, axis=1)\n    return np.mean(np.where(avg_price_per_path-K<0, 0, avg_price_per_path-K))*np.exp(-r*T)'}
            </SyntaxHighlighter>
                 <h2>FULL CODE</h2>
                 <SyntaxHighlighter className="code-block-highlighter" language="python" style={tomorrowNight} showLineNumbers={true}>
            {'import numpy as np\nimport pandas as pd\nfrom scipy.stats import t\nfrom matplotlib import pyplot as plt\n\ndef get_paths(S0, mu, sigma, times, num_paths):\n    """\n        : input float S0 : Inital Stock Price\n        : input float mu : Average drift\n        : input float sigma : Volatility\n        : input float[] times : Array of time steps in time order\n        : input int num_paths : Number of paths to generate\n\n        : return 2d numpy.array : 2D numpy array of shape(num_paths, len(times))\n    """\n    init = np.tile([S0],(num_paths,1))\n    stand_term = np.tile(np.diff(times) * (mu - 1/2 * sigma**2), (num_paths,1))\n    stoch_term = sigma * np.random.normal(loc=0,\n                                          scale=np.diff(times),\n                                          size=(num_paths,len(times)-1))\n    mc_step = stand_term + stoch_term\n    return np.append(init, S0 * np.exp(np.cumsum(mc_step, axis=1)), axis=1)\n\ndef calc_limits(paths, confidence):\n    """\n        : input 2d numpy.array paths : shape(num_paths, len(times))\n        : input float confidence : Confidence as a proportion from 0 to 1\n\n        : return numpy.array, numpy.array : hi limits, lo limits\n    """\n    mean = np.mean(paths, axis=0)\n    std = np.std(paths, axis=0)\n    lo = t.ppf(1-confidence, len(paths), loc = mean, scale = std)\n    hi = t.ppf(confidence, len(paths), loc = mean, scale = std)\n    return mean, hi, lo\n\ndef calc_price_asian_call_option(paths, K, r, T):\n    avg_price_per_path = np.mean(paths, axis=1)\n    return np.mean(np.where(avg_price_per_path-K<0, 0, avg_price_per_path-K))*np.exp(-r*T)\n\nif __name__ == "__main__":\n    # Initialize variables\n    S0 = 100\n    mu = 0.1\n    sigma = 0.15\n    times = np.linspace(0,1,100)\n    num_paths = 300\n\n    K = 101\n    r = .005\n\n    # Calculate paths that follow Geometric Brownian Motion\n    paths = get_paths(S0, mu, sigma, times, num_paths)\n\n    # Calculate price of asian call option\n    asian_call_price = calc_price_asian_call_option(paths, K, r, times[-1] - times[0])\n\n    # Calcualte hi and lo limits from generated paths\n    mean_path, hi_lim, lo_lim = calc_limits(paths, .95)\n\n    textStr = "$N_{paths}=$ %d\\n$S_{0}=$ %.2f\\n$\mu$= %.4f\\n$\sigma$ = %.4f"%(num_paths,S0,mu,sigma)\n\n    fig, ax = plt.subplots()\n    props = dict(boxstyle="square", facecolor="white", alpha=1.0)\n\n    ax.set_title("Monte-Carlo Simulation | Stock Price vs. Time",\n                 loc="left", \n                 fontsize=12,\n                 pad=20)\n    ax.text(0.95, 0.05, textStr, transform=ax.transAxes,\n                                 fontsize=10,\n                                 verticalalignment="bottom",\n                                 horizontalalignment="right",\n                                 bbox=props)\n\n    # Plot all paths as gray lines\n    for path in paths:\n        ax.plot(times, path, color="gray", alpha=.1)\n\n    # Plot mean path as a red line\n    ax.plot(times, mean_path, color="red", alpha=.5, label="$\overline {S_i}$")\n\n    # Plot hi and lo limits as blue dashed lines\n    ax.plot(times, hi_lim, color="blue", linestyle="--", alpha=.5, label="95% CI")\n    ax.plot(times, lo_lim, color="blue", linestyle="--", alpha=.5)\n\n    ax.set_ylabel("Stock Price")\n    ax.set_xlabel("T")\n    ax.legend(prop = {"size": 12 \})\n\n    for item in ([ax.title, ax.xaxis.label, ax.yaxis.label] + ax.get_xticklabels() + ax.get_yticklabels()):\n        item.set_fontsize(12)\n\n    # Show plot\n    fig.set_size_inches(6,6)\n    fig.savefig("MC_paths.png")'}
            </SyntaxHighlighter>
           </div>

};

export default MCOptionPage;