import React, { useState, useEffect } from 'react';
import Tex2SVG from "react-hook-mathjax";
import {Plot, LineSeries, Legend, Axis, Heading} from 'react-plot';

const ImpliedVolCalc = ({ }) => {

    const [impliedVol, setImpliedVol] = useState(0);
    const [guess, setGuess] = useState([{"x":.1 , "y":.1} ,{"x": .5, "y":.1},{"x":1 , "y":.1}]);
    const [Pline, setPline] = useState([{"x":.1 , "y":.1},{"x":1 , "y":.1}]);
    const [bestGuessline, setBestGuessline] = useState([{"x":.5 , "y":0},{"x":.1 , "y":1}]);
    const [isMobile, setIsMobile] = useState(false);
    const [yMAX, setYMAX] = useState(1);
    const [yMIN, setYMIN] = useState(.1);

    const [xMAX, setXMAX] = useState(1);
    const [xMIN, setXMIN] = useState(.1);


    const [plotWidth, setPlotWidth] = useState(300);
    const [riskFreeRate, setRiskFreeRate] = useState(0);
    const [optionPos, setOptionPos] = useState(0);
    const [P, setP] = useState(50);
    const [s0, setS0] = useState(100);
    const [K, setK] = useState(101);
    const [T, setT] = useState(10);


    const ncdf = (x) => {
        var t = 1 / (1 + .2315419 * Math.abs(x));
        var d =.3989423 * Math.exp( -x * x / 2);
        var prob = d * t * (.3193815 + t * ( -.3565638 + t * (1.781478 + t * (-1.821256 + t * 1.330274))));
        if( x > 0 ) prob = 1 - prob;
        return prob;
      };

      const calcBlackScholesOrig = (sigma) => {
        let d1 = 0;
        let d2 = 0;
        if(sigma != 0)
        {
           d1 = Math.log(s0/K) + (riskFreeRate+sigma*sigma/2) * T / (sigma*Math.sqrt(T));
           d2 = d1 - sigma * Math.sqrt(T);
        }else
        {
          d1 = Math.log(s0/K);
          d2 = d1;
        }

        if(optionPos == 0)
        {
            return (s0 * ncdf(d1) - Math.exp(-riskFreeRate * T) * K * ncdf(d2));
        }else{
            return Math.exp(-riskFreeRate * T) * K * ncdf(-d2) - s0 * ncdf(-d1);
        }
    };
    const calcBlackScholes = (sigma) => {
        let d1 = Math.log(s0/K) + (riskFreeRate+sigma*sigma/2) * T / (sigma*Math.sqrt(T));
        let d2 = d1 - sigma * Math.sqrt(T);

        if(optionPos == 0)
        {
            return Math.abs((s0 * ncdf(d1) - Math.exp(-riskFreeRate * T) * K * ncdf(d2)) - P);
        }else{
            return Math.abs((Math.exp(-riskFreeRate * T) * K * ncdf(-d2) - s0 * ncdf(-d1))-P);
        }
    };
    const calcBlackScholesPrime = (sigma) => {
        let deltaSigma = .001;
        return (calcBlackScholes(sigma+deltaSigma) - calcBlackScholes(sigma-deltaSigma))/(2*deltaSigma);
    };

    const calcImpliedVolatility = () => {
        setGuess([]);

        let max_val = 0;
        let min_val = 100;

        let max_val_x = 0;
        let min_val_x = 100;
        let max_iterations = 200;
        let P_BS = [];
        min_val_x = 0;
        for(let i = 0; i < 300; i++)
        {
            let current_y = calcBlackScholesOrig(i/300);
            P_BS.push({"x" : i/300.0, "y" : current_y})
            if(current_y > max_val)
            {
                max_val = current_y;
            }
            if(current_y < min_val)
            {
                min_val = current_y;
            }
            if(current_y - s0 < .001)
            {
                max_val_x = i/300;
            }
        }

        let new_guesses = [ {"x" : .5, "y" : calcBlackScholesOrig(.5)}];
        
        for(let i = 0; i < max_iterations; i++)
        {
            let current_guess = new_guesses[new_guesses.length-1].x;
            let current_y = calcBlackScholes(current_guess);

            let next_guess = current_guess - current_y / calcBlackScholesPrime(current_guess);
            new_guesses.push({"x" : next_guess, "y" : calcBlackScholesOrig(current_guess)});
        }

        setGuess(P_BS);
        setYMAX(max_val+1);
        setYMIN(min_val-1);
        setXMAX(max_val_x + .1);
        setXMIN(min_val_x - .1);

        setImpliedVol(parseInt(1000*new_guesses[new_guesses.length-1].x)/1000.0);

        setPline([{"x" : (min_val_x-.1), "y" : P},{"x" : (max_val_x+.1), "y" : P}]);
        setBestGuessline([{"x" : new_guesses[new_guesses.length-1].x, "y" : min_val-1},{"x" : new_guesses[new_guesses.length-1].x, "y" : max_val+1}]);
    };

    useEffect(()=> {
        calcImpliedVolatility();
    }, [optionPos, K, s0, P, riskFreeRate, T]); 

    const handlePosInput = (e) => {
        setOptionPos(e.target.value);
    };

    const handleCurrencyInput = (e) => {
        if(e.target.value.length == 0)return;
        e.target.value = parseInt(100*e.target.value)/100;
        if(e.target.value < 0)
        {
            e.target.value = 0;
        }
        if(e.target.attributes.name.nodeValue === "s0"){
            setS0(parseFloat(e.target.value));
        }else if(e.target.attributes.name.nodeValue ===  "K"){
            setK(parseFloat(e.target.value));
        }else if(e.target.attributes.name.nodeValue ===  "P"){
            setP(parseFloat(e.target.value));
        }
    };

    const handleTimeInput = (e) => {
        if(e.target.value.length == 0)return;
        if(e.target.value < 0)
        {
            e.target.value = 0;
        }
        if(e.target.value > 100)
        {
            e.target.value = 100;
        }
        e.target.value = e.target.value;
        setT(parseFloat(e.target.value));
    };
    
    const handleRiskFreeInput = (e) => {
        if(e.target.value.length == 0)return;
        if(e.target.value < -1)
        {
            e.target.value = -1;
        }else if(e.target.value > 1){
            e.target.value = 1;
        }
        setRiskFreeRate(parseFloat(e.target.value));
    };

    useEffect(() => {
        const handleResize = () => {
          let f = .33
          if(window.innerWidth < 700)
          {
            setIsMobile(true);
            f = .6;
          }else{
            setIsMobile(false);
          }

          let w = f * window.innerWidth;
          if(w > 300)
          {
            setPlotWidth(300);
          }else
          {
            setPlotWidth(w);
          }
        };
        handleResize();
        window.addEventListener("resize", handleResize);
        return () => window.removeEventListener("resize", handleResize);
      }, []);

      if(!isMobile)
      {
        return <div className="mc-option-container">
                <div className="mc-option-parent">
                    <div className="mc-option-container-left">
                        IV CALCULATOR  <br></br> <br></br>
                        Option Type: <br></br>
                        <select onChange={handlePosInput} name="option_position"> 
                             <option name="Call" value="0" >Call</option>
                             <option name="Put" value="1" >Put</option>
                        </select>  <br></br>
                        <table className="mc-option-menu-table">
                            <tr> 
                            <td><a><Tex2SVG display="inline" latex="S(0)"/></a></td>
                            <td><input className="mc-option-pricer-number-input" onBlur={handleCurrencyInput} name="s0" type="number" placeholder="100" /></td>
                            </tr>
                            <tr> 
                            <td><a><Tex2SVG display="inline" latex="K"/></a></td>
                            <td><input className="mc-option-pricer-number-input"  onBlur={handleCurrencyInput} name="K" type="number" placeholder="101"/></td>
                            </tr>
                            <tr> 
                            <td><a><Tex2SVG display="inline" latex="P_{opt}"/></a></td>
                            <td><input className="mc-option-pricer-number-input" onBlur={handleCurrencyInput} name="P" type="number" placeholder="50" /></td>
                            </tr>
                            <tr> 
                            <td><a><Tex2SVG display="inline" latex="r_{free}"/></a></td>
                            <td><input className="mc-option-pricer-number-input"  onBlur={handleRiskFreeInput} type="number" placeholder="0"/></td>
                            </tr>
                            <tr> 
                            <td><a><Tex2SVG display="inline" latex="T"/></a></td>
                            <td><input className="mc-option-pricer-number-input"  onBlur={handleTimeInput} type="number" placeholder="10"/></td>
                            </tr>
                        </table>
                    </div>
                    <div className="mc-option-container-right">
                        <div className='mc-path-display'>
                        <Plot
    width={plotWidth}
    height={plotWidth}
    margin={{ bottom: 0, left: 0, top: 0, right: 0 }}
  >
    <Heading
      title="Implied Volatility Calculator"
      subtitle="Volatility vs P_BS"
    />
    <LineSeries
      data={guess}
      xAxis="x"
      yAxis="y"
      lineStyle={{ stroke: 'black', opacity: .9 }}
      label=""
      displayMarker={false}
    />
    <LineSeries
      data={Pline}
      xAxis="x"
      yAxis="y"
      lineStyle={{ stroke: 'red', opacity: .9 }}
      label=""
      displayMarker={false}
    />
    <LineSeries
      data={bestGuessline}
      xAxis="x"
      yAxis="y"
      lineStyle={{ stroke: 'red', opacity: .9 }}
      label=""
      displayMarker={false}
    />
   
    <Axis
      id="y"
      position="left"
      label="P_BS"
      displayPrimaryGridLines
      min={yMIN}
      max={yMAX}
    />
    <Axis
      id="x"
      position="bottom"
      label="Volatility"
      displayPrimaryGridLines
      min={xMIN}
      max={xMAX}
    />
  </Plot>
                        </div>
                        <div className="mc-option-pricer-result">
                            Implied Vol: {impliedVol}
                        </div>
                    </div>
                </div>
           </div>
    }
    
    return <div className="mc-option-container-mobile">
    <div className="mc-option-parent-mobile">
        <div className="mc-option-container-top-mobile">
            <div className='mc-path-display-mobile'>
                        <Plot
    width={plotWidth}
    height={plotWidth}
    margin={{ bottom: 0, left: 0, top: 0, right: 0 }}
  >
    <Heading
      title="Implied Volatility Calculator"
      subtitle="Volatility vs P_BS"
    />
    <LineSeries
      data={guess}
      xAxis="x"
      yAxis="y"
      lineStyle={{ stroke: 'black', opacity: .9 }}
      label=""
      displayMarker={false}
    />
    <LineSeries
      data={Pline}
      xAxis="x"
      yAxis="y"
      lineStyle={{ stroke: 'red', opacity: .9 }}
      label=""
      displayMarker={false}
    />
    <LineSeries
      data={bestGuessline}
      xAxis="x"
      yAxis="y"
      lineStyle={{ stroke: 'red', opacity: .9 }}
      label=""
      displayMarker={false}
    />
   
    <Axis
      id="y"
      position="left"
      label="P_BS"
      displayPrimaryGridLines
      min={yMIN}
      max={yMAX}
    />
    <Axis
      id="x"
      position="bottom"
      label="Volatility"
      displayPrimaryGridLines
      min={xMIN}
      max={xMAX}
    />
  </Plot>
                    </div>
                    <div className="mc-option-pricer-result">
                    Implied Vol: {impliedVol}.
                    </div>
        </div>
        <div className="mc-option-container-bottom-mobile">
            IV CALCULATOR   <br></br> <br></br>
            Option Type: <br></br>
            <select onChange={handlePosInput} name="option_position"> 
                 <option name="Call" value="0" >Call</option>
                 <option name="Put" value="1" >Put</option>
            </select>  <br></br>
            <table className="mc-option-menu-table">
                    <tr> 
                    <td><a><Tex2SVG display="inline" latex="S(0)"/></a></td>
                    <td><input className="mc-option-pricer-number-input-mobile" onBlur={handleCurrencyInput} name="s0" type="number" placeholder="100" /></td>
                    </tr>
                    <tr> 
                    <td><a><Tex2SVG display="inline" latex="K"/></a></td>
                    <td><input className="mc-option-pricer-number-input-mobile"  onBlur={handleCurrencyInput} name="K" type="number" placeholder="101"/></td>
                    </tr>
                    <tr> 
                    <td><a><Tex2SVG display="inline" latex="P_{opt}"/></a></td>
                    <td><input className="mc-option-pricer-number-input-mobile" onBlur={handleCurrencyInput} name="P" type="number" placeholder="50" /></td>
                    </tr>
                    <tr> 
                    <td><a><Tex2SVG display="inline" latex="r_{free}"/></a></td>
                    <td><input className="mc-option-pricer-number-input-mobile"  onBlur={handleRiskFreeInput} type="number" placeholder="0"/></td>
                    </tr>
                    <tr> 
                    <td><a><Tex2SVG display="inline" latex="T"/></a></td>
                    <td><input className="mc-option-pricer-number-input-mobile"  onBlur={handleTimeInput} type="number" placeholder="10"/></td>
                    </tr>
            </table>
        </div>
    </div>
</div>
};

export default ImpliedVolCalc;
