import React, { useState, useEffect } from 'react';
import CustomConsole from "./CustomConsole";
import SubjectPage from './ProjectPages/SubjectPage';
import MCOptionPage from './ProjectPages/MCOptionPage';
import ImpVolPage from './ProjectPages/ImpVolPage';
import VolFitterPage from './ProjectPages/VolFitterPage';

const PageManager = ({ page }) => {
    if(page === "home"){
        return <CustomConsole />;
    }else if(page == "quant"){
        return <div> 
                    <div><a  className="home-button" href="?page=home">{'<'}{'<'} RETURN HOME </a></div>
                    <SubjectPage projects={["MC OPTION PRICER", "IMPLIED VOLATILITY", "VOL CURVE FITTER"]} project_jsx={[<MCOptionPage/>,  <ImpVolPage/>, <VolFitterPage/>]} project_index={0} />
                </div>
    }else if(page == "monte-carlo-option-pricer"){
        return  <div>
                    <div><a  className="home-button" href="?page=home">{'<'}{'<'} RETURN HOME </a></div>
                    <MCOptionPage/>
                </div>
    }else if(page == "implied-volatility-calculator"){
        return  <div>
                    <div><a  className="home-button" href="?page=home">{'<'}{'<'} RETURN HOME </a></div>
                    <ImpVolPage/>
                </div>
    }else if(page == "vol-fitter"){
        return  <div>
                    <div><a  className="home-button" href="?page=home">{'<'}{'<'} RETURN HOME </a></div>
                    <VolFitterPage/>
                </div>
    }


    return <span> {page} </span>;
};

export default PageManager;