import React, { useState } from 'react';
import Tex2SVG from "react-hook-mathjax";
import ImpliedVolCalc from '../ProjectCode/ImpliedVolCalc';
import SyntaxHighlighter from 'react-syntax-highlighter';
import { docco, tomorrowNight } from 'react-syntax-highlighter/dist/esm/styles/hljs';

const ImpVolPage = ({ }) => {

    return <div className="page-info-container">
            <ImpliedVolCalc />
            <h2>VOLATILITY</h2>
            <div> 
                <p className="page-info-block">Stock prices fluctuate randomly as trades occur with each tick of the stock exchanges. The size of these changes are linked to how <a className="custom-console-blue">volatile</a> the market for that stock is at the time. Larger jumps denote a more volatile stock with inherently more risk. As a result, volatility modeling is key to focasting stock prices and pricing options. In most models, volatility cannot be directly observed, leaving us with,
                <li><a className="custom-console-blue">Implied Volatility</a> - Predicted future volatility based on market's expectations of price fluctuations.</li>
                <li><a className="custom-console-blue">Realized Volatility</a> - Calculated past volatility based on the actual price flucations in the market (or on the log returns).</li>
                Implied volatility can be calculated using the market's current pricing of options. Since volatility is an input to all option pricing models, volatility can be implied by inverting the model from the prices of available options on the market.  
                </p>
            </div>
            <h2>BLACK-SCHOLES MODEL</h2>
            <div> 
                <p className="page-info-block">The simplest model used in option pricing is the Black-Scholes model. In the basic Black-Scholes model, the following assumptions are made,
                <li><a className="custom-console-yellow">Assumption #1</a>: Volatility is constant. </li>
                <li><a className="custom-console-yellow">Assumption #2</a>: Changes in stock price follow a geomatric brownian motion. </li>
                <li><a className="custom-console-yellow">Assumption #3</a>: The market is arbitrage-free. </li>
                The model takes as input the parameters of the option contract (i.e. strike and time to expiry) and state of the market (i.e. risk-free interst rate, volatility, initial stock price) and outputs an option price. The equations to calculate the Black-Scholes option price are,
                <p className="page-eq-center-block"><Tex2SVG display="block" latex="d_1 = \ln(\frac{S}{K}) + \frac{(r + \frac{\sigma^2}{2})T}{\sigma \sqrt{T}}"/>, </p>
                <p className="page-eq-center-block"><Tex2SVG display="block" latex="d_2 = d_1 - \sigma \sqrt{T}"/>, </p>
                <p className="page-eq-center-block"><Tex2SVG display="block" latex="P_{CALL} = S_0 N(d_1) - K e^{-rT} N(d_2)"/>, or</p>
                <p className="page-eq-center-block"><Tex2SVG display="block" latex="P_{PUT} = K e^{-rT} N(-d_2) - S_0 N(-d_1)"/>.</p>
                Where the variables above are,
                <li><Tex2SVG display="inline" latex="N"/> - Normal Cummulative Distribution Function</li>
                <li><Tex2SVG display="inline" latex="K"/> - Strike Price</li>
                <li><Tex2SVG display="inline" latex="\sigma"/> - Volatility</li>
                <li><Tex2SVG display="inline" latex="S_0"/> - Initial Stock Price</li>
                <li><Tex2SVG display="inline" latex="T"/> - Time to expiry</li>
                <li><Tex2SVG display="inline" latex="r"/> - Interest rate (risk-free rate of return).</li>
                </p>
            </div>
            <p className="page-info-block">Below is function in python that will calculate the price of a call or put option using the Black-Scholes model. Using the SciPy library, the implementation of the above equations is very simple.
            </p>
            <SyntaxHighlighter className="code-block-highlighter" language="python" style={tomorrowNight} showLineNumbers={true}>
            {'import numpy as np\nfrom scipy.stats import norm\n\ndef calc_black_scholes_option(S0, K, sigma, r, T, type):\n    """\n        : input float S0 : Inital Stock Price\n        : input float K : Strike Price\n        : input float sigma : Volatility\n        : input float r  : Risk Free Rate\n        : input float T : Time to Expiry\n        : input str   type : Type of Option ("call" or "put")\n\n        : return float : B.S. Option Price\n    """\n    d1 = np.log(S0/K)+(r+sigma**2/2)*T/(sigma*T**(1/2))\n    d2 = d1 - sigma * T**(1/2)\n    if(type == "call"):\n        return S0*norm.cdf(d1) - np.exp(-r*T)*K*norm.cdf(d2)\n    elif(type == "put"):\n        return np.exp(-r*T)*K*norm.cdf(-d2) - S0*norm.cdf(-d1)\n    else:\n        raise Exception("Type must be `call` or `put`.")'}
            </SyntaxHighlighter>
            <h2>IMPLIED VOLATILITY</h2>
            <p className="page-info-block">
            Given the price of an option available on the market, <Tex2SVG display="inline" latex="P_{true}"/>, the implied volatility can be calculated by finding the value of <Tex2SVG display="inline" latex="\sigma"/> that would give <Tex2SVG display="inline" latex="P_{true}"/> using the function above, <Tex2SVG display="inline" latex="P_{B.S.}"/>. This ultimately comes down to finding the root of the function <Tex2SVG display="inline" latex="f(\sigma) = |P_{B.S.} - P_{true}|"/>. SciPy has a built-in root finding algorithm that can be called to solve this problem in a two-line function.</p>
             <SyntaxHighlighter className="code-block-highlighter" language="python" style={tomorrowNight} showLineNumbers={true}>
            {'from scipy.optimize import minimize_scalar\ndef calc_implied_volatility_easy(P, S0, K, r, T, type):\n    """\n        : input float P : Inital Stock Price\n        : input float S0 : Inital Stock Price\n        : input float K : Strike Price\n        : input float r  : Risk Free Rate\n        : input float T : Time to Expiry\n        : input str   type : Type of Option ("call" or "put")\n\n        : return float : Implied Volatility\n    """\n    delta = lambda sigma : abs(calc_black_scholes_option(S0,K,sigma,r,T,type)-P)\n    return minimize_scalar(delta, bounds=(0,5), method=bounded).x\n'}
            </SyntaxHighlighter>
            <p className="page-info-block">
            But, for the sake of completion, it can also be done using Newton's Method to find the roots of <Tex2SVG display="inline" latex="f(\sigma) = |P_{B.S.} - P_{true}|"/>. Newton's method involves making an initial guess and updating the guess based on the function and it's first derivative,</p>
            <p className="page-eq-center-block"><Tex2SVG display="block" latex="\sigma_{n+1} = \sigma_{n} - \frac{f(\sigma_n)}{f'(\sigma_n)}"/></p>
            <p className="page-info-block">The code below can be used to find the implied volatility to within a tolerance of the true value.</p>
            <SyntaxHighlighter className="code-block-highlighter" language="python" style={tomorrowNight} showLineNumbers={true}>
            {'def calc_implied_volatility_newton(P, S0, K, r, T, type, tolerance):\n    """\n        : input float P : Inital Stock Price\n        : input float S0 : Inital Stock Price\n        : input float K : Strike Price\n        : input float r  : Risk Free Rate\n        : input float T : Time to Expiry\n        : input str   type : Type of Option ("call" or "put")\n\n        : return float : Implied Volatility\n    """\n    d_sigma = tolerance/100\n    max_iterations= int(1/tolerance)\n    def f_x(sigma):\n        return abs(calc_black_scholes_option(S0,K,sigma+d_sigma,r,T,type)-P)\n\n    def f_prime_x(sigma):\n        return (f_x(sigma+d_sigma) - f_x(sigma-d_sigma)) / (2*d_sigma)\n\n    sigma_guess = .05\n    for i in range(0, max_iterations):\n        if(f_x(sigma_guess) < tolerance):\n            return sigma_guess\n        else:\n            sigma_guess = sigma_guess - f_x(sigma_guess)/f_prime_x(sigma_guess)\n\n    return sigma_guess\n'}
            </SyntaxHighlighter>

            <h2>VOLATILITY SMILE</h2>
            <div  className="page-img-block"><img className="page-img" width="50%" src={require("../implied_vol.png")}></img></div>
            <p className="page-info-block">
            If the market actually followed the Black-Scholes model for option pricing, volatility would be a constant independent of the strike or time to expiration. However, in reality, due to the skewness (3rd moment) and kurtosis (4th moment) of the distribution of returns, the implied volatility is not constant. Instead, the implied volatility shows a dependence on strike and time to expirtation. This dependence gives rise to the <a className="custom-console-blue">volatility smile</a>, where the implied volatility decreases as the strike becomes "at the money" and increases once it passes this point. Below is some Python code to plot the implied volatility for option contracts in MSFT that were available on September 25, 2024. Option contracts are fetched using the YFinance python package to interface to the YahooFinance API. 
            </p>
            <SyntaxHighlighter className="code-block-highlighter" language="python" style={tomorrowNight} showLineNumbers={true}>
                {'import pandas as pd\nimport datetime as dt\nimport yfinance as yf\nfrom matplotlib import pyplot as plt\nif __name__ == "__main__":\n\n    # Getting MSFT Stock Ticker from YFinance API\n    msft = yf.Ticker("MSFT")\n\n    # Storing the Current Stock Price\n    S0 = msft.history()["Close"].iloc[-1]\n\n    # Getting first expiration in list.\n    exp = msft.options[0]\n\n    # Create an empty df to store calls\n    all_calls = pd.DataFrame()\n\n    # Calculating days to expiration and adding it as a column to the df\n    all_calls = msft.option_chain(exp).calls\n    all_calls["exp"] = pd.to_datetime(exp) + pd.DateOffset(hours=23, minutes=59, seconds=59)\n    all_calls["today"] = pd.to_datetime(dt.datetime.today())\n    all_calls["daysToExpiration"]=(all_calls.exp - all_calls.today).dt.days + 1\n\n    # Creating an empty array to store implied volatility\n    implied_vols = []\n    for row in all_calls.itertuples():\n        # Calculating implied volatility from our function\n        implied_vol = calc_implied_volatility_easy(row.lastPrice, S0, row.strike,\n                                                   0.00, row.daysToExpiration, "call")\n        implied_vols.append(implied_vol)\n\n    fig, ax = plt.subplots()\n    props = dict(boxstyle="square", facecolor="white", alpha=1.0)\n\n    ax.set_title("MSFT | Implied Vol vs. Strike",loc="left", fontsize=12, pad=20)\n\n    # Plot all paths as gray lines\n    ax.scatter(all_calls.strike, implied_vols, color="black", label="calculated")\n\n    ax.set_ylabel("Implied Vol")\n    ax.set_xlabel("Strike")\n    ax.legend(prop = {"size": 12 })\n\n    for item in ([ax.title, ax.xaxis.label, ax.yaxis.label] + ax.get_xticklabels() + ax.get_yticklabels()):\n        item.set_fontsize(12)\n\n    # Show plot\n    fig.set_size_inches(6,6)\n    fig.savefig("implied_vol.png")\n'}
            </SyntaxHighlighter>
            <h2>FULL CODE</h2>
            <SyntaxHighlighter className="code-block-highlighter" language="python" style={tomorrowNight} showLineNumbers={true}>
                {'import numpy as np\nfrom scipy.stats import norm\nfrom scipy.optimize import minimize_scalar  \nimport pandas as pd\nimport datetime as dt\nimport yfinance as yf\nfrom matplotlib import pyplot as plt\n\n\ndef calc_black_scholes_option(S0, K, sigma, r, T, type):\n    """\n        : input float S0 : Inital Stock Price\n        : input float K : Strike Price\n        : input float sigma : Volatility\n        : input float r  : Risk Free Rate\n        : input float T : Time to Expiry\n        : input str   type : Type of Option ("call" or "put")\n\n        : return float : B.S. Option Price\n    """\n    d1 = np.log(S0/K)+(r+sigma**2/2)*T/(sigma*T**(1/2))\n    d2 = d1 - sigma * T**(1/2)\n    if(type == "call"):\n        return S0*norm.cdf(d1) - np.exp(-r*T)*K*norm.cdf(d2)\n    elif(type == "put"):\n        return np.exp(-r*T)*K*norm.cdf(-d2) - S0*norm.cdf(-d1)\n    else:\n        raise Exception("Type must be `call` or `put`.")  \n\ndef calc_implied_volatility_easy(P, S0, K, r, T, type):\n    """\n        : input float P : Inital Stock Price\n        : input float S0 : Inital Stock Price\n        : input float K : Strike Price\n        : input float r  : Risk Free Rate\n        : input float T : Time to Expiry\n        : input str   type : Type of Option ("call" or "put")\n\n        : return float : Implied Volatility\n    """\n    delta = lambda sigma : abs(calc_black_scholes_option(S0,K,sigma,r,T,type)-P)\n    return minimize_scalar(delta, bounds=(0,5), method="bounded").x\n\ndef calc_implied_volatility_newton(P, S0, K, r, T, type, tolerance):\n    """\n        : input float P : Inital Stock Price\n        : input float S0 : Inital Stock Price\n        : input float K : Strike Price\n        : input float r  : Risk Free Rate\n        : input float T : Time to Expiry\n        : input str   type : Type of Option ("call" or "put")\n\n        : return float : Implied Volatility\n    """\n    d_sigma = tolerance/100\n    max_iterations= int(1/tolerance)\n    def f_x(sigma):\n        return abs(calc_black_scholes_option(S0,K,sigma+d_sigma,r,T,type)-P)\n\n    def f_prime_x(sigma):\n        return (f_x(sigma+d_sigma) - f_x(sigma-d_sigma)) / (2*d_sigma\n)\n\n    sigma_guess = .05\n    for i in range(0, max_iterations):\n        if(f_x(sigma_guess) < tolerance):\n            return sigma_guess\n        else:\n            sigma_guess = sigma_guess - f_x(sigma_guess)/f_prime_x(sigma_guess)\n\n    return sigma_guess\n\n\n\nif __name__ == "__main__":\n    \n    # Getting MSFT Stock Ticker from YFinance API\n    msft = yf.Ticker("MSFT")\n\n    # Storing the Current Stock Price\n    S0 = msft.history()["Close"].iloc[-1]\n\n    # Getting first expiration in list.\n    exp = msft.options[0]\n\n    # Create an empty df to store calls\n    all_calls = pd.DataFrame()\n\n    # Calculating days to expiration and adding it as a column to the df\n    all_calls = msft.option_chain(exp).calls\n    all_calls["exp"] = pd.to_datetime(exp) + pd.DateOffset(hours=23, minutes=59, seconds=59)\n    all_calls["today"] = pd.to_datetime(dt.datetime.today())\n    all_calls["daysToExpiration"]=(all_calls.exp - all_calls.today).dt.days + 1\n\n    # Creating an empty array to store implied volatility\n    implied_vols = []\n    for row in all_calls.itertuples():\n        # Calculating implied volatility from our function\n        implied_vol = calc_implied_volatility_easy(row.lastPrice, S0, row.strike, 0.00, row.daysToExpiration, "call")\n        implied_vols.append(implied_vol)\n\n    # Plot Implied Volatility vs Strike\n    fig, ax = plt.subplots()\n    props = dict(boxstyle="square", facecolor="white", alpha=1.0)\n\n    ax.set_title("MSFT | Implied Vol vs. Strike",loc="left", fontsize=12, pad=20)\n\n    # Plot all paths as gray lines\n    ax.scatter(all_calls.strike, implied_vols, color="black", label="calculated")\n\n    ax.set_ylabel("Implied Vol")\n    ax.set_xlabel("Strike")\n    ax.legend(prop = {"size": 12 })\n\n    for item in ([ax.title, ax.xaxis.label, ax.yaxis.label] + ax.get_xticklabels() + ax.get_yticklabels()):\n        item.set_fontsize(12)\n\n    # Show plot\n    fig.set_size_inches(6,6)\n    fig.savefig("implied_vol.png")'}
            </SyntaxHighlighter>
           </div>

};

export default ImpVolPage;