import React, { useState, useEffect } from 'react';
import Tex2SVG from "react-hook-mathjax";
import Plot from 'react-plotly.js';

const VolFitterDisplay = ({ }) => {
    
    const [plotWidth, setPlotWidth] = useState(550);
    const [xdata, setXData] = useState([0,1,2]);
    const [ydata, setYData] = useState([0,1,2]);
    const [zdata, setZData] = useState([0,1,2]);

    const [getData, setGetData] = useState(true);
    const [xdata_fit, setXDataFit] = useState([0,1,2]);
    const [ydata_fit, setYDataFit] = useState([0,1,2]);
    const [zdata_fit, setZDataFit] = useState([[0,0,0],[1,1,1],[2,2,2]]);
    const [isMobile, setIsMobile] = useState(false);

    useEffect(() => {
        const handleResize = () => {
          let f = .66
          if(window.innerWidth < 700)
          {
            setIsMobile(true);
            f = .66;
          }else{
            setIsMobile(false);
          }

          let w = f * window.innerWidth;
          if(w > 550)
          {
            setPlotWidth(550);
          }else
          {
            setPlotWidth(w);
          }
        };
        handleResize();
        window.addEventListener("resize", handleResize);
        return () => window.removeEventListener("resize", handleResize);
      }, []);
      const loadData = function(){
        fetch( './TSLA_data.csv' )
            .then( response => response.text() )
            .then( responseText => {
              let rows = responseText.split("\n");
              let xData = [];
              let yData = [];
              let zData = [];
              for(let i = 0; i < rows.length; i+=2) {
                let lineComps = rows[i].split(",");
                xData.push(parseFloat(lineComps[0]));
                yData.push(parseFloat(lineComps[1]));
                zData.push(parseFloat(lineComps[2]));
              }
              setXData(xData);
              setYData(yData);
              setZData(zData);
            });
    };
    const loadFit = function(){
      fetch( './TSLA_surface.csv' )
          .then( response => response.text() )
          .then( responseText => {
              let rows = responseText.split("\n");
                let xData = [];
                let yData = [];
                let zData = [];
                let zData_row = [];
                for(let i = 0; i < rows.length; i+=2) {
                  let lineComps = rows[i].split(",");
                  if(xData.length == 0)
                  {
                      xData.push(parseFloat(lineComps[0]));
                      yData.push(parseFloat(lineComps[1]));
                      zData_row.push(parseFloat(lineComps[2]));
                      continue
                  }
                  if(!xData.includes(parseFloat(lineComps[0])))
                  {
                      xData.push(parseFloat(lineComps[0]));
                  }
                  if(parseFloat(lineComps[1]) !== yData[yData.length - 1])
                  {
                      yData.push(parseFloat(lineComps[1]));
                      zData.push(zData_row);
                      zData_row=[];
                  }
                  zData_row.push(parseFloat(lineComps[2]));
                }
                setXDataFit(xData);
                setYDataFit(yData);
                setZDataFit(zData);
          });
        };
    if(getData)
    {
        setGetData(false);
        loadFit();
        loadData();
    }
    return <div className="mc-option-container">
                <Plot
                    data={[
                        {
                            x: xdata,
                            y: ydata,
                            z: zdata,
                            type: 'scatter3d',
                            mode: 'markers',
                            marker: {color: '#fff', opacity:.5, size : 2},
                            name: 'Data'
                        },
                        {
                            x: xdata_fit,
                            y: ydata_fit,
                            z: zdata_fit,
                            type: 'surface',
                            name: 'Fit'
                        }
                    ]}
                    
                    layout={{
                        font: {color : '#fff'},
                        width: plotWidth,
                        height: plotWidth,
                        title: 'TSLA Volatility Surface',
                        paper_bgcolor: '#242931',
                        showlegend: true,
                        legend: {
                          x: 0,
                          xanchor: 'left',
                          y: 1
                        },
                        scene:{
                            aspectratio: {
                                x: 1,
                                y: 1,
                                z: 1,
                              },
                            xaxis : {
                                title: {
                                    text: "log(S/K)"
                                },
                                color : '#fff',
                                range: [-1,1],
                            },
                            yaxis : {
                                title: {
                                    text: "T"
                                },
                                color : '#fff',
                                range: [0,1],
                                scaleanchor: 'x', // Link the y-axis scaling to the x-axis
                                scaleratio: 1 
                            },
                            zaxis : {
                                title: {
                                    text: "Implied Volatility"
                                },
                                color : '#fff',
                                range: [0,5],
                                scaleanchor: 'x', // Link the y-axis scaling to the x-axis
                                scaleratio: 1 
                            }
                        }
                    }}
                />
            </div>
};

export default VolFitterDisplay;
